html {
	font-size: 16px;
	font-weight: 400;
}
body {
	font-family: $mainFont;
	color: $black;
}

a {
	color: $black;
	text-decoration: none;
}

svg {
	width: auto;
	height: auto;
}

input, button {
	-webkit-appearance: none;
}

input,
button,
select,
textarea {
	font-family: $mainFont;
	background-color: transparent;
	border: 0;
	border-radius: 0;
	outline: 0;
}

.container {
	width: 1270px;
	padding: 0 15px;
	margin: 0 auto;
	max-width: 100%;
	box-sizing: border-box;
}